import * as React from "react"
import { DataObserverContext } from "./DataObserver"

export type FramerFunction<P = any> = (props: P) => Partial<P>

export function WithFramerFunction<T>(Component: React.ComponentType<T>, transform: FramerFunction<T>) {
    return function(props: T) {
        return (
            <DataObserverContext.Consumer>
                {() => {
                    return <Component {...props} {...transform(props)} />
                }}
            </DataObserverContext.Consumer>
        )
    }
}
