export function isEqual(a: any, b: any, deep: boolean = true): boolean {
    if (a === b) {
        return true
    }
    if (!a || !b) {
        return false
    }

    const typeA = typeof a
    const typeB = typeof b
    if (typeA !== typeB) {
        return false
    }

    if (typeA !== "object") {
        return a === b
    }

    if (a instanceof Array) {
        if (!(b instanceof Array)) {
            return false
        }
        const aLength = a.length
        const bLength = b.length
        if (aLength !== bLength) {
            return false
        }
        for (let i = 0; i < aLength; i++) {
            if (!isEqual(a[i], b[i])) {
                return false
            }
        }
    }

    const aEquals = a.equals
    const bEquals = b.equals
    if (aEquals && bEquals && aEquals instanceof Function) {
        return a.equals(b)
    }

    if (a instanceof Map || a instanceof Set || b instanceof Map || b instanceof Set) {
        throw Error("Not implemented equals for Map or Set")
    }

    const aKeys = Object.keys(a)
    const bKeys = Object.keys(b)
    if (aKeys.length !== bKeys.length) {
        return false
    }

    for (const key of aKeys) {
        const valueA = a[key]
        const valueB = b[key]
        if (deep && !isEqual(valueA, valueB)) {
            return false
        }
        // tslint:disable-next-line:triple-equals
        if (!deep && valueA != valueB) {
            return false
        }
    }

    return true
}
