import { Interpolation } from "./Interpolation"
import { NumberInterpolation } from "./number"
import { ObjectInterpolation } from "./object"
import { AnimatableInterpolation } from "./animatable"
import { isAnimatable } from "../animation/Animatable"
import { NoInterpolation } from "./none"

export function interpolationForValue(value: any): Interpolation<any> {
    const type = typeof value
    if (type === "number") {
        return NumberInterpolation
    } else if (type === "object") {
        return ObjectInterpolation
    } else if (type === "boolean" || type === "function") {
        return NoInterpolation
    } else if (isAnimatable(value)) {
        return AnimatableInterpolation
    }
    console.warn(`No interpolation available for ${value}`)
    return NoInterpolation
}

export const ValueInterpolation: Interpolation<any> = {
    interpolate<T>(from: T, to: T): ((progress: number) => T) {
        return interpolationForValue(from).interpolate(from, to)
    },
    span<T>(from: T, to: T): number {
        return interpolationForValue(from).span(from, to)
    },
}
