import { PropertyControls, ControlType, verifyPropertyControls } from "../types/PropertyControls"
import { JSONObject } from "../types/JSONData"
import { FramerFunction } from "../../data/WithFramerFunction"

// Public

export type PackageIdentifier = string
export type ComponentIdentifier = string
export type ComponentDefinition<P = any> = {
    file: string
    packageIdentifier: string // one package can contain multiple components
    identifier: ComponentIdentifier
    name: string
    type: ComponentType
    external: boolean // installed as dependency

    class: React.ComponentType<P> | JSON | FramerFunction<any>
    properties?: PropertyControls<P>
    defaults?: P
}

export type ReactComponentDefinition<P = any> = ComponentDefinition<P> & { class: React.ComponentType<P> }
export type DesignComponentDefinition = ComponentDefinition & { class: JSONObject }

export function isReactDefinition<P = any>(d: ComponentDefinition): d is ReactComponentDefinition<P> {
    return d.type !== "master"
}

export function isDesignDefinition(d: ComponentDefinition): d is DesignComponentDefinition {
    return d.type === "master"
}

export function isNonUserFacing(d: ComponentDefinition): boolean {
    return d.type === "device" || d.type === "deviceSkin" || d.type === "deviceHand"
}

// NOTE: Also defined as ComponentType in the Server project
export type ComponentType = "component" | "device" | "deviceSkin" | "deviceHand" | "master" | "function"

export interface ComponentLoader {
    packageDisplayName(packageId: PackageIdentifier): string | undefined
    localPackageIdentifier(): PackageIdentifier
    packageIdentifiers(): PackageIdentifier[]
    componentsForPackage(identifier: string): ComponentDefinition[]
    componentForIdentifier(identifier: ComponentIdentifier): ComponentDefinition | null
    componentIdentifiers(): ComponentIdentifier[]
    forEachDesignComponents(cb: (component: DesignComponentDefinition) => void): void
    forEachComponent(cb: (component: ComponentDefinition) => boolean): void
}

export const componentLoader: ComponentLoader = {
    packageDisplayName: (packageId: PackageIdentifier): string | undefined => {
        return undefined
    },
    localPackageIdentifier: () => {
        return localPackageFallbackIdentifier
    },
    packageIdentifiers: (): PackageIdentifier[] => {
        return []
    },
    componentsForPackage: (identifier: PackageIdentifier) => {
        return []
    },
    componentForIdentifier: (identifier: ComponentIdentifier) => {
        return null
    },
    componentIdentifiers: () => {
        return []
    },
    forEachDesignComponents: (cb: (component: DesignComponentDefinition) => void): void => {},
    forEachComponent: (cb: (component: ComponentDefinition) => boolean): void => {},
}

export const updateComponentLoader = (script: string, framer: any) => {
    updateComponentLoaderWithScript(script, framer)
}

// Private

// Imported to re-export to script
import * as React from "react"
import * as ReactDOM from "react-dom"
import { SetSizeAndPositionChildren } from "../presentation/SetSizeAndPositionChildren"

interface ObjectMap<T> {
    [key: string]: T
}

type LazyMap<T> = ObjectMap<() => T>

interface Package {
    __framer__?: PackageInfo // Automatically added to project and to dependencies
}

interface PackageJson {
    name?: string // Can be undefined for the local package
    framer?: {
        components?: ComponentInfo[]
        displayName?: string
    }
    design?: any
}

const localPackageFallbackIdentifier = "|local|" // Contains characters that do not exist in normal names

interface PackageInfo {
    packageJson: PackageJson
    sourceModules?: LazyMap<SourceModule> // Indicates an editable package, key is the filename in components folder
    dependencies?: LazyMap<Package>
}

interface SourceModule {
    __info__: ComponentInfo[]
}

// TODO share a type with the Server here
type ComponentInfo = {
    name: string
    children: boolean
    type: ComponentType
}

type ComponentMap = ObjectMap<ComponentDefinition>
const packagesNamesMap: ObjectMap<string> = {}

// Export for tests
export const updateComponentLoaderWithScript = (script: string, framer: any) => {
    // A special require function with the dependencies we'd like to feed
    const require = (name: string) => {
        if (name === "react") {
            return React
        }
        if (name === "react-dom" || name === "ReactDOM") {
            return ReactDOM
        }
        if (name === "framer") {
            return framer
        }
        throw Error(`Component loader: Can't require ${name}`)
    }

    const fn = new Function("exports", "require", script)
    const packageExports = {} as Package

    fn(packageExports, require)

    const componentMap = componentsFromPackage(packageExports)
    const packageComponentMap: { [key: string]: ComponentDefinition[] } = {}

    for (let key of Object.keys(componentMap)) {
        const componentDefinition: ComponentDefinition = componentMap[key]
        const packageIdentifier = componentDefinition.packageIdentifier
        if (!packageComponentMap[packageIdentifier]) {
            packageComponentMap[packageIdentifier] = []
        }
        packageComponentMap[packageIdentifier].push(componentDefinition)
    }

    componentLoader.packageDisplayName = (packageId: PackageIdentifier): string | undefined => {
        return packagesNamesMap[packageId]
    }

    componentLoader.localPackageIdentifier = (): PackageIdentifier => {
        if (!packageExports.__framer__) return localPackageFallbackIdentifier
        const { packageJson } = packageExports.__framer__
        if (!packageJson) return localPackageFallbackIdentifier
        return packageJson.name || localPackageFallbackIdentifier
    }

    componentLoader.packageIdentifiers = (): PackageIdentifier[] => {
        return Object.keys(packageComponentMap)
    }

    componentLoader.componentsForPackage = (identifier: PackageIdentifier): ComponentDefinition[] => {
        return packageComponentMap[identifier] || []
    }

    componentLoader.componentForIdentifier = (identifier: ComponentIdentifier): ComponentDefinition => {
        return componentMap[identifier]
    }

    componentLoader.componentIdentifiers = (): ComponentIdentifier[] => {
        return Object.keys(componentMap)
    }

    componentLoader.forEachDesignComponents = (cb: (component: DesignComponentDefinition) => void): void => {
        for (let k in packageComponentMap) {
            for (let component of packageComponentMap[k]) {
                if (component.type !== "master") continue
                cb(component as DesignComponentDefinition)
            }
        }
    }

    componentLoader.forEachComponent = (cb: (component: ComponentDefinition) => boolean): void => {
        for (let k in packageComponentMap) {
            for (let component of packageComponentMap[k]) {
                const abort = cb(component)
                if (abort) {
                    break
                }
            }
        }
    }
}

const componentsFromPackage = (
    packageExports: Package,
    isExternal: boolean = false,
    identifierPrefix: string = ""
): ComponentMap => {
    const componentMap = {}
    const packageInfo = packageExports.__framer__

    if (!packageInfo) {
        // No extra info, nothing we can do
        return {}
    } // else

    const { packageJson, sourceModules, dependencies } = packageInfo

    if (
        packageJson.framer &&
        packageJson.framer.displayName &&
        packageJson.name &&
        !packagesNamesMap[packageJson.name]
    ) {
        packagesNamesMap[packageJson.name] = packageJson.framer.displayName
    }

    let packageName = packageJson.name
    if (!isExternal && !packageName) {
        packageName = localPackageFallbackIdentifier
    }
    if (!packageName) {
        return {} // If we cant identify the package, we refuse to map. This will not happen unless something is broken.
    }

    // First look at the sourceModules
    if (sourceModules && Object.keys(sourceModules).length > 0) {
        for (const fileName of Object.keys(sourceModules)) {
            // TODO: Handle exceptions when trying to load
            const moduleExports = sourceModules[fileName]() as SourceModule
            const componentsInfo = moduleExports.__info__

            if (!componentsInfo) {
                // No components in this module?
                continue
            } // else

            Object.assign(
                componentMap,
                componentsFromExports(
                    packageName,
                    moduleExports,
                    componentsInfo,
                    `${identifierPrefix}${fileName}_`,
                    isExternal,
                    fileName
                )
            )
        }
    }
    // If not found, inspect the manual info
    // TODO: Merge manual info to find more properties
    else if (packageJson.framer && packageJson.framer.components) {
        Object.assign(
            componentMap,
            componentsFromExports(
                packageName,
                packageExports,
                packageJson.framer.components,
                identifierPrefix,
                isExternal
            )
        )
    }

    // Process dependencies for the main project package only
    if (!isExternal && dependencies) {
        for (const dependencyKey of Object.keys(dependencies)) {
            // TODO: Sandbox dependencies
            Object.assign(
                componentMap,
                componentsFromPackage(dependencies[dependencyKey](), true, `${identifierPrefix}${dependencyKey}/`)
            )
        }
    }

    if (isExternal && packageJson.design) {
        const res = mastersFromPackage(packageJson.design, packageName)
        let count = 1
        const components = res.map(master => {
            return {
                identifier: `${identifierPrefix}${master.id}`,
                packageIdentifier: packageName,
                name: master.name || packageName + "-" + count++,
                class: master,
                type: "master",
                file: "design/document.json",
            }
        })
        components.forEach(component => (componentMap[component.identifier] = component))
    }

    return componentMap
}

function mastersFromPackage(json: any, packageName: string) {
    const masters: any[] = []
    findMasters(json.root, packageName, masters)
    return masters
}

export function findMasters(node: any, packageName: string, masters: any[]) {
    if (!node.id) return
    if (node.replicaInfo) return
    if (node.isExternalMaster && node.isExternalMaster !== packageName) return

    if (node.isMaster === true) {
        node.parentid = null
        node.isExternalMaster = packageName
        node.top = 0
        node.left = 0
        node.bottom = null
        node.right = null
        updateJSONMaster(node, packageName, null)
        masters.push(node)
        return
    }

    const children = node.children
    if (!(children instanceof Array)) return

    for (let i = 0, il = children.length; i < il; i++) {
        findMasters(children[i], packageName, masters)
    }
}

// We would want to use HardCodedCodeIdentifier here, but that's part of Vekter for now
const builtInCodeComponents = ["framer/Scroll", "framer/Page", "framer/Stack"]

function updateJSONMaster(node: any, packageName: string, parentid: string | null): void {
    node.id = packageName + "-" + node.id
    node.parentid = parentid
    if (node.fillImage) {
        node.fillImage = "node_modules/" + packageName + "/design/images/" + node.fillImage
    }
    if (node.codeComponentIdentifier && builtInCodeComponents.indexOf(node.codeComponentIdentifier) === -1) {
        node.codeComponentIdentifier = packageName + "/" + node.codeComponentIdentifier
    }
    const children = node.children
    if (!(children instanceof Array)) return

    for (let i = 0, il = children.length; i < il; i++) {
        updateJSONMaster(children[i], packageName, node.id)
    }
}

const componentsFromExports = (
    packageIdentifier: string,
    exports: Package | SourceModule,
    componentsInfo: ComponentInfo[],
    baseIdentifier: string,
    external: boolean,
    sourceFile?: string
): ComponentMap => {
    const componentMap = {}

    for (const componentInfo of componentsInfo) {
        const componentObject = exports[componentInfo.name]

        if (!componentObject) {
            // Component defined but not found in exports!
            continue
        } // else

        const componentDef = componentWithInfo(
            baseIdentifier,
            packageIdentifier,
            componentObject,
            componentInfo,
            external,
            sourceFile
        )
        componentMap[componentDef.identifier] = componentDef
    }

    return componentMap
}

const componentWithInfo = (
    baseIdentifier: string,
    packageIdentifier: string,
    object: any,
    componentInfo: ComponentInfo,
    external: boolean,
    file: string = ""
): ComponentDefinition => {
    const { name, children, type } = componentInfo
    let properties: PropertyControls<any> = {}
    if (typeof object.propertyControls === "object") {
        properties = verifyPropertyControls(object.propertyControls)
    }
    if (children) {
        properties["children"] = { title: "Children", type: ControlType.Children }
    }
    let componentClass = object
    if (type === undefined || type === "component") {
        componentClass = SetSizeAndPositionChildren(componentClass)
    }
    return {
        identifier: `${baseIdentifier}${name}`,
        name,
        type,
        class: componentClass,
        properties,
        defaults: {}, // TODO
        file,
        external,
        packageIdentifier,
    }
}
