import { Interpolation } from "./Interpolation"

export const difference = (a: number, b: number): number => {
    return Math.abs(a - b)
}

export const NumberInterpolation: Interpolation<number> = {
    interpolate(from: number, to: number): ((progress: number) => number) {
        const a1 = +from
        const b1 = to - a1
        return (progress: number): number => {
            let value = a1 + b1 * progress
            return value
        }
    },
    span(from: number, to: number): number {
        return from - to
    },
}
