import { Interpolation } from "./Interpolation"
import { interpolationForValue } from "./value"

export const difference = (a: number, b: number): number => {
    return Math.abs(a - b)
}

export const ObjectInterpolation: Interpolation<object> = {
    interpolate(from: object, to: object): ((progress: number) => object) {
        const result = { ...from }
        const interpolations = { ...from }
        for (let key in from) {
            interpolations[key] = interpolationForValue(from[key])
        }

        return (progress: number): object => {
            for (let key in from) {
                result[key] = interpolations[key].interpolate(from[key], to[key])(progress)
            }
            return result
        }
    },
    span(from: object, to: object): number {
        const interpolations = { ...from }
        for (let key in from) {
            interpolations[key] = interpolationForValue(from[key])
        }
        let value = 0
        for (let key in from) {
            const span = interpolations[key].span(from[key], to[key])
            if (Math.abs(value) < Math.abs(span)) {
                value = span
            }
        }
        return value
    },
}
