import { Animator } from "./Animator"
import { Interpolation, Interpolator } from "../../interpolation/Interpolation"
import { ValueInterpolation } from "../../interpolation/value"

export const LinearDefaults = {
    duration: 1,
}
export type Options = typeof LinearDefaults

export class LinearAnimator<Value> implements Animator<Value, Options> {
    current: Value
    destination: Value
    interpolator: Interpolator<Value>
    progress = 0
    constructor(
        private options: Options = LinearDefaults,
        private interpolation: Interpolation<Value> = ValueInterpolation
    ) {}

    setFrom(value: Value) {
        this.current = value
        this.updateInterpolator()
    }

    setTo(value: Value) {
        this.destination = value
        this.updateInterpolator()
    }

    isReady(): boolean {
        return this.interpolator !== undefined
    }

    updateInterpolator() {
        if (this.current === undefined || this.destination === undefined) {
            return
        }
        this.interpolator = this.interpolation.interpolate(this.current, this.destination)
    }

    next = (delta: number): Value => {
        this.progress += delta / this.options.duration
        this.current = this.interpolator(this.progress)
        return this.current
    }

    isFinished(): boolean {
        return this.progress >= 1
    }
}
