import { Interpolation } from "./Interpolation"
import { interpolationForValue } from "./value"
import { Animatable } from "../animation/Animatable"

export const AnimatableInterpolation: Interpolation<Animatable<any>> = {
    interpolate<Value>(from: Animatable<Value>, to: Animatable<Value>): ((progress: number) => Animatable<Value>) {
        const fromValue = from.get()
        const toValue = to.get()
        const result = Animatable(fromValue)
        const interpolation = interpolationForValue(fromValue)

        return (progress: number): Animatable<any> => {
            const value = interpolation.interpolate(fromValue, toValue)(progress)
            result.set(value)
            return result
        }
    },
    span<Value>(from: Animatable<Value>, to: Animatable<Value>): number {
        const value = from.get()
        const interpolation = interpolationForValue(value)
        return interpolation.span(value, to.get())
    },
}
