import { Observer } from "../animation/Animatable/Observers"
import { Cancel, AnimatableObject } from "../animation/Animatable"
import { ObservableObject } from "./ObservableObject"

export function Data<T extends object = object>(initial: Partial<T> | object = {}): AnimatableObject<T> {
    const data = ObservableObject(initial, true, false)
    Data.addData(data)
    return data
}

export namespace Data {
    export let stores: object[] = []
    export function addData(data: object) {
        stores.push(data)
    }
    export const addObserver = <T extends object>(target: T, observer: Observer<T>): Cancel => {
        return ObservableObject.addObserver(target, observer)
    }
}
