import * as React from "react"

export interface TouchCursorStyleProperties {
    targetClass?: string
}

export class TouchCursorStyle extends React.Component<TouchCursorStyleProperties> {
    static defaultProps: TouchCursorStyleProperties = {
        targetClass: "framer-touch-cursor-target",
    }

    render() {
        return (
            <style key="TouchCursorStyle">{`
                .${this.props.targetClass} {
                    cursor: url("cursors/cursor.png") 32 32, auto;
                    cursor: -webkit-image-set(url("cursors/cursor.png") 1x, url("cursors/cursor@2x.png") 2x) 32 32, auto;
                }

                .${this.props.targetClass}:active {
                    cursor: url("cursors/cursor-active.png") 32 32, auto;
                    cursor: -webkit-image-set(url("cursors/cursor-active.png") 1x, url("cursors/cursor-active@2x.png") 2x) 32 32, auto;
                }
            `}</style>
        )
    }
}
