import * as React from "react"
import { Scroll } from "./Scroll"
import { AnimatableObject, Animatable } from "../animation/Animatable"
import { InertialScroll } from "../animation/Animators"
import { MainLoopAnimationDriver } from "../animation/Drivers/MainLoopDriver"
import { Rect } from "../render"
import { isRectProviding } from "./utils/RectProvider"
import { animate } from "../animation/animate"
import { FramerAnimation } from "../animation/FramerAnimation"

type ScrollAnimation = MainLoopAnimationDriver<InertialScroll, number, any>

export interface Props {
    parentSize: AnimatableObject<{ width: number; height: number }>
}

export class Page extends React.Component<Props> {
    static _stylableContainer = true
    static supportsConstraints = true
    pages: Rect[]
    animation: FramerAnimation | null

    nearestPageIndex = (position: number): number => {
        const distance = function(rect: Rect): number {
            return Math.abs(rect.x + position)
        }

        const closestPages = this.pages.sort((a, b) => distance(a) - distance(b))
        return this.pages.indexOf(closestPages[0])
    }

    onDragAnimationStart = (animation: { x: ScrollAnimation; y: ScrollAnimation }, draggable: any) => {
        animation.x.cancel()
        animation.y.cancel()
        this.cancelAnimation()
        const index = this.nearestPageIndex(draggable.x.get())
        const currentPage = this.pages[index]
        this.animation = animate.spring(draggable.x, -currentPage.x, { tension: 500, friction: 50 })
    }

    onDragSessionStart = () => {
        this.cancelAnimation()
    }

    private cancelAnimation = () => {
        if (this.animation) {
            this.animation.cancel()
            this.animation = null
        }
    }

    render() {
        const { children } = this.props
        this.pages = []
        const content = React.Children.count(children) === 1 && React.Children.only((this.props.children as any)[0])
        if (content && typeof content.type !== "string") {
            const type = content.type
            if (isRectProviding(type)) {
                let frame = type.rect(content.props)
                // XXX some major assumptions here:
                // - pages are all the same size
                // - pages are the same size as the Page component
                // - pages fill the entire content
                // - component only scrolls horizontal
                const parentWidth = Animatable.getNumber(this.props.parentSize.width)
                const parentHeight = Animatable.getNumber(this.props.parentSize.height)
                for (let i = 0; i < frame.width / parentWidth; i++) {
                    this.pages.push({ x: i * parentWidth, y: 0, width: parentWidth, height: parentHeight })
                }
            }
        }

        return (
            <Scroll
                parentSize={this.props.parentSize}
                width={"100%"}
                height={"100%"}
                direction={"horizontal"}
                onDragAnimationStart={this.onDragAnimationStart}
                onDragSessionStart={this.onDragSessionStart}
            >
                {children}
            </Scroll>
        )
    }
}
