import * as React from "react"
import { AnimatableObject } from "../animation/Animatable"
import { animate } from "../animation/animate"
import { Frame, FrameProperties } from "../components/Frame"
import { ObservableObject } from "../data/ObservableObject"
import { Spring } from "../animation/Animators"
import { FramerAnimation } from "../animation/FramerAnimation"
import { Size } from "../render"

export interface Props {
    // tslint:disable:react-unused-props-and-state
    contextSize: Size
    contentSize: Size
    initialProps: Partial<FrameProperties>
    transitionProps: Partial<FrameProperties>
    animationDuration: number
    visible: boolean
    hideAfterTransition: boolean

    onTapBackdrop: (() => void) | undefined
    backdropColor: string | undefined
}

export interface State {
    frameProps: AnimatableObject<FrameProperties & { dimOpacity: number }> | undefined
    latestAnimation: FramerAnimation | undefined
    visible: boolean
}

const springAnimationOptions = { tension: 500, friction: 50, tolerance: 1 }

export class NavigationContainer extends React.Component<Props, State> {
    state: State = {
        frameProps: undefined,
        latestAnimation: undefined,
        visible: true,
    }

    componentWillUnmount() {
        const { latestAnimation } = this.state
        if (latestAnimation && !latestAnimation.isFinished) {
            latestAnimation.cancel()
        }
    }

    onTransitionEnd = () => {
        if (this.props.hideAfterTransition) {
            this.setState({ ...this.state, visible: false })
        }
    }

    componentDidUpdate() {
        if (this.state.latestAnimation && !this.state.latestAnimation.isFinished()) {
            this.state.latestAnimation.onfinish = this.onTransitionEnd
        }
    }

    static getDerivedStateFromProps(props: Props, previousState: State): State | null {
        if (previousState.latestAnimation && !previousState.latestAnimation.isFinished) {
            previousState.latestAnimation.cancel()
        }

        if (previousState.frameProps === undefined) {
            const frameProps = ObservableObject(
                { ...{ ...allAnimatableProperties, top: 0, left: 0, dimOpacity: 0 }, ...props.initialProps },
                true
            )
            let latestAnimation: FramerAnimation | undefined
            const dimOpacity = props.hideAfterTransition ? 0 : 1
            if (props.animationDuration <= 0) {
                Object.assign(frameProps, allAnimatableProperties, props.transitionProps, { dimOpacity })
            } else {
                latestAnimation = animate(
                    frameProps,
                    { ...allAnimatableProperties, ...props.transitionProps, dimOpacity },
                    Spring,
                    springAnimationOptions
                )
            }
            return {
                frameProps,
                latestAnimation,
                visible: props.visible && !(props.animationDuration <= 0 && props.hideAfterTransition),
            }
        } else if (props.animationDuration <= 0) {
            Object.assign(previousState.frameProps, allAnimatableProperties, props.transitionProps, {
                dimOpacity: props.hideAfterTransition ? 0 : 1,
            })
            return { ...previousState, visible: props.visible && !props.hideAfterTransition }
        } else {
            const latestAnimation = animate(
                previousState.frameProps,
                { ...allAnimatableProperties, ...props.transitionProps, dimOpacity: props.hideAfterTransition ? 0 : 1 },
                Spring,
                springAnimationOptions
            )
            return {
                ...previousState,
                latestAnimation,
                visible: props.visible,
            }
        }
    }

    render() {
        const { contextSize, contentSize, backdropColor, onTapBackdrop } = this.props

        return (
            <Frame
                width={contextSize.width}
                height={contextSize.height}
                visible={this.state.visible}
                background={"transparent"}
                overflow={"hidden"}
                style={{ WebkitPerspective: 1200 }} // TODO: add perspective to Frame
            >
                <Frame
                    width={contextSize.width}
                    height={contextSize.height}
                    opacity={this.state.frameProps ? this.state.frameProps.dimOpacity : 0}
                    background={backdropColor ? backdropColor : "transparent"}
                    onTap={onTapBackdrop}
                />
                <Frame
                    {...this.state.frameProps as FrameProperties}
                    width={contentSize.width}
                    height={contentSize.height}
                    background={"transparent"}
                    style={{ WebkitBackfaceVisibility: "hidden" }} // TODO: add WebkitBackfaceVisibility to Frame
                >
                    {this.props.children}
                </Frame>
            </Frame>
        )
    }
}

const allAnimatableProperties: Partial<FrameProperties> = {
    top: 0,
    left: 0,
    z: 0,
    rotation: 0,
    rotationX: 0,
    rotationY: 0,
    rotationZ: 0,
    scale: 1,
    scaleX: 1,
    scaleY: 1,
    scaleZ: 1,
    skew: 0,
    skewX: 0,
    skewY: 0,
    originX: 0.5,
    originY: 0.5,
    originZ: 0.5,
    opacity: 1,
}
