import * as React from "react"
import { FramerEventSession } from "../events/FramerEventSession"
import { FrameProperties, Frame } from "./Frame"
import { FramerEventListener, FramerEvent } from "../events"
import { WithEventsProperties } from "./hoc/WithEvents"
import { TouchCursorStyle } from "./TouchCursorStyle"

export interface ScreenProperties {
    width: number
    height: number
    scale?: number
    color?: string
    touch?: boolean
}

export class Screen extends React.Component<ScreenProperties> {
    session = new FramerEventSession(this.dispatcher)
    private frameRef = React.createRef<React.Component<FrameProperties & Partial<WithEventsProperties>>>()

    componentDidMount() {
        if (this.frameRef.current) {
            const withEvents = this.frameRef.current as any
            if (withEvents.component.current) {
                this.session.originElement = withEvents.component.current.element
            }
        }
    }

    render() {
        const useTouchCursor = this.props.touch
        const className = useTouchCursor ? TouchCursorStyle.defaultProps.targetClass : undefined

        // Apply the touch cursor, or set the default cursor (so no text cursors appear):
        const touchCursorStyle: JSX.Element | null = useTouchCursor ? <TouchCursorStyle /> : null
        const style: React.CSSProperties = useTouchCursor
            ? {}
            : {
                  cursor: "default",
              }
        const frame = (
            <Frame
                className={className}
                ref={this.frameRef}
                originX={0}
                originY={0}
                width={this.props.width}
                height={this.props.height}
                scale={this.props.scale}
                background={this.props.color || "none"}
                style={style}
            >
                {this.props.children}
            </Frame>
        )

        return (
            <FramerEventListener session={this.session}>
                {touchCursorStyle}
                {frame}
            </FramerEventListener>
        )
    }

    private dispatcher(type: string, event: FramerEvent, target: EventTarget) {
        target.dispatchEvent(new CustomEvent("FramerEvent", { bubbles: true, detail: { type: type, event: event } }))
    }
}
