import { FramerAnimation } from "./FramerAnimation"
import { Linear, Spring, TensionFrictionSpringOptions } from "./Animators"
import { Interpolation } from "../interpolation/Interpolation"
import { Animatable, AnimatableObject } from "./Animatable"
import { AnimatorClass } from "./Animators/Animator"
import { isAnimatable } from "./Animatable"

export function animate<Value, Options>(
    from: Animatable<Value> | AnimatableObject<Value>,
    to: Value | AnimatableObject<Value>,
    animator?: AnimatorClass<Value, Options>,
    options?: Partial<Options>,
    interpolation?: Interpolation<Value>
): FramerAnimation {
    const target = from
    let fromValue: Value
    if (isAnimatable(from)) {
        fromValue = from.get()
    } else {
        fromValue = Animatable.objectToValues(from)
    }
    const animation = new FramerAnimation(target, fromValue, to, animator, options, interpolation)
    animation.play()
    return animation
}

export namespace animate {
    export function linear<Value>(
        from: Animatable<Value> | AnimatableObject<Value>,
        to: Value | AnimatableObject<Value>,
        duration = 1,
        interpolation?: Interpolation<Value>
    ): FramerAnimation {
        return animate(from, to, Linear, { duration }, interpolation)
    }

    export function spring<Value>(
        from: Animatable<Value> | AnimatableObject<Value>,
        to: Value | AnimatableObject<Value>,
        options?: Partial<TensionFrictionSpringOptions>,
        interpolation?: Interpolation<Value>
    ): FramerAnimation {
        return animate(from, to, Spring, options, interpolation)
    }
}
