import { PathSegmentRecord } from "./PathSegmentRecord"
import { Point } from ".."

export namespace PathSegment {
    export type HandleMirroring = "straight" | "symmetric" | "disconnected" | "asymmetric"
    // Note: more additions to this namespace below
}

export class PathSegment extends PathSegmentRecord {
    x: number // The anchor point of the segment.
    y: number
    handleMirroring: PathSegment.HandleMirroring
    handleOutX: number // Describes the out tangent of the segment.
    handleOutY: number
    handleInX: number // Describes the in tangent of the segment.
    handleInY: number
    radius: number

    toJS() {
        const JS = super.toJS()
        JS["__class"] = this.constructor.name
        return JS
    }

    toJSON() {
        return this.toJS()
    }
}

export namespace PathSegment {
    export const point = (pathSegment: PathSegment) => {
        return { x: pathSegment.x, y: pathSegment.y }
    }

    export const handleOut = (pathSegment: PathSegment) => {
        return { x: pathSegment.handleOutX, y: pathSegment.handleOutY }
    }

    export const handleIn = (pathSegment: PathSegment) => {
        return { x: pathSegment.handleInX, y: pathSegment.handleInY }
    }

    export const calculatedHandleOut = (pathSegment: PathSegment): Point => {
        switch (pathSegment.handleMirroring) {
            case "symmetric":
            case "disconnected":
            case "asymmetric":
                return Point.add(point(pathSegment), handleOut(pathSegment))
            default:
                return { x: pathSegment.x, y: pathSegment.y }
        }
    }

    export const calculatedHandleIn = (pathSegment: PathSegment): Point => {
        switch (pathSegment.handleMirroring) {
            case "symmetric":
                return Point.subtract(point(pathSegment), handleOut(pathSegment))
            case "disconnected":
            case "asymmetric":
                return Point.add(point(pathSegment), handleIn(pathSegment))
            default:
                return point(pathSegment)
        }
    }

    export const curveDefault = (points: PathSegment[], index: number): Point => {
        if (points.length > 2) {
            let pointBefore: PathSegment
            let pointAfter: PathSegment

            if (index === 0) {
                pointBefore = points[points.length - 1]
            } else {
                pointBefore = points[index - 1]
            }

            if (index === points.length - 1) {
                pointAfter = points[0]
            } else {
                pointAfter = points[index + 1]
            }

            const delta = Point.subtract(point(pointAfter), point(pointBefore))
            return { x: delta.x / 4, y: delta.y / 4 }
        }

        return { x: 10, y: 10 }
    }

    const length = (point: Point) => {
        return Math.sqrt(point.x * point.x + point.y * point.y)
    }

    export const multiply = (point: PathSegment, value: number): PathSegment => {
        return new PathSegment(
            point.merge({
                x: point.x * value,
                y: point.y * value,
                handleOutX: point.handleOutX * value,
                handleOutY: point.handleOutY * value,
                handleInX: point.handleInX * value,
                handleInY: point.handleInY * value,
            })
        )
    }

    export const assymetricPoint = (opposite: Point, size: Point): Point => {
        const oppositeSize = length(opposite)
        const selfSize = length(size)
        const ratio = selfSize / oppositeSize
        const reverse = Point.reverse(opposite)
        return { x: reverse.x * ratio, y: reverse.y * ratio }
    }

    export const reflectAlongLine = (point: PathSegment, lineStart: Point, lineEnd: Point): PathSegment => {
        const dx = lineStart.x - lineEnd.x
        const dy = lineStart.y - lineEnd.y
        const a = (dx * dx - dy * dy) / (dx * dx + dy * dy)
        const b = 2 * dx * dy / (dx * dx + dy * dy)

        const x = Math.round(a * (point.x - lineStart.x) + b * (point.y - lineStart.y) + lineStart.x)
        const y = Math.round(b * (point.x - lineStart.x) - a * (point.y - lineStart.y) + lineStart.y)

        const oriCtrl1 = calculatedHandleOut(point)
        const oriCtrl2 = calculatedHandleIn(point)
        const absControl1X = Math.round(a * (oriCtrl1.x - lineStart.x) + b * (oriCtrl1.y - lineStart.y) + lineStart.x)
        const absControl1Y = Math.round(b * (oriCtrl1.x - lineStart.x) - a * (oriCtrl1.y - lineStart.y) + lineStart.y)
        const absControl2X = Math.round(a * (oriCtrl2.x - lineStart.x) + b * (oriCtrl2.y - lineStart.y) + lineStart.x)
        const absControl2Y = Math.round(b * (oriCtrl2.x - lineStart.x) - a * (oriCtrl2.y - lineStart.y) + lineStart.y)
        const relControl1 = Point.subtract({ x: absControl1X, y: absControl1Y }, { x: x, y: y })
        const relControl2 = Point.subtract({ x: absControl2X, y: absControl2Y }, { x: x, y: y })

        return new PathSegment(
            PathSegmentRecord(point).merge({
                x: x,
                y: y,
                handleOutX: relControl2.x,
                handleOutY: relControl2.y,
                handleInX: relControl1.x,
                handleInY: relControl1.y,
            })
        )
    }
}
